import "./assets/css/fonts.css";
import "./assets/css/App.css";
import "./assets/css/slider.css";

import React, { useState, useEffect } from "react";
import Header from "./components/layout/Header.js";
import Carrello from "./components/Carrello/Carrello.js";
import Shop from "./components/Shop/Shop.js";
import Slider from "./components/Slider";
import { BrowserRouter as Router, Route } from "react-router-dom";

export const CarrelloContext = React.createContext([]);

function App() {
  const [totale, setTotale] = useState(0);
  const [items, setItems] = useState([]);
  const [carrelloItems, setCarrelloItems] = useState([]);

  const buyItem = id => {
    let found = false;
    const newCarrelloItems = carrelloItems.map(carrelloItem => {
      if (carrelloItem.id === id) {
        carrelloItem.quantity++;
        found = true;
      }
      return carrelloItem;
    });
    if (found) setCarrelloItems([...newCarrelloItems]);
    else setCarrelloItems([...carrelloItems, { ...getItem(id), quantity: 1 }]);
    calcolaTotale();
  };

  // const getCartItem = id => {
  //   return carrelloItems.find(carrelloItem => carrelloItem.id === id);
  // };

  const getItem = id => {
    return items.find(item => item.id === id);
  };

  const removeItem = id => {
    const newCarrelloItems = carrelloItems.map(carrelloItem => {
      if (carrelloItem.id === id) {
        carrelloItem.quantity--;
      }
      return carrelloItem;
    });
    setCarrelloItems(
      newCarrelloItems.filter(carrelloItem => carrelloItem.quantity > 0)
    );
    calcolaTotale();
  };

  // const calcolaTotale = () => {
  //     setTotale(carrelloItems.reduce((accumulator, carrelloItem) => {
  //       return accumulator + (carrelloItem.quantity * carrelloItem.cost);
  //     }, 0));
  //   };

  const calcolaTotale = () => {
    let newTotale = carrelloItems.reduce((accumulator, carrelloItem) => {
      return accumulator + carrelloItem.quantity * carrelloItem.cost;
    }, 0);
    console.log(newTotale);
    newTotale = Math.abs(newTotale.toFixed(2));
    setTotale(newTotale);
  };

  const checkout = () => {
    setCarrelloItems([]);
    setTotale(0);
  };

  // useEffect(() => {
  //   fetch("https://5db179198087400014d38a73.mockapi.io/api/v1/products")
  //     .then(res => res.json())
  //     .then(json => setItems(json));
  // }, []);

  useEffect(() => {
    const fetchdata = async () => {
      const res = await fetch(
        "https://5db179198087400014d38a73.mockapi.io/api/v1/products"
      );
      setItems(await res.json());
    };
    fetchdata();
  }, []);

  return (
    <div className="App">
      <Router>
        <Header />
        <Route exact path={["/", "/ecomm_react"]} render={() => <Slider />} />
        <Route
          path="/carrello"
          render={() => (
            <Carrello
              totale={totale}
              carrelloItems={carrelloItems}
              buyItem={buyItem}
              removeItem={removeItem}
              checkout={checkout}
            />
          )}
        />
        <CarrelloContext.Provider value={carrelloItems}>
          <Route
            path="/shop"
            render={() => (
              <Shop items={items} buyItem={buyItem} removeItem={removeItem} />
            )}
          />
        </CarrelloContext.Provider>
      </Router>
    </div>
  );
}

export default App;
